.meanings{
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
    height: 55vh;
    border: 10px solid rgb(105, 105, 105);
    border-radius:10px;
    padding: 10px 20px;
    overflow-x: hidden;
}

.subTitle{
    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;
    font-size: 2vw;
}

.singleMean{
    display: flex;
    flex-direction: column;
    border-radius: 10px;
    padding: 10px 20px;
    margin: 10px 0;
}

@media (max-width:900px) {
    .meanings{
        height:60vh;
        overflow-x: hidden;
        overflow-y: scroll;
    }
    
}